*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

.pagina-imobiliare-interior{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background: linear-gradient(rgba(0, 0, 0, 0.70), rgba(0, 0, 0, 0.50)), url(/src/imagini/imobiliare-background-1.jpg) ;
    background-size: cover;
}
.footer-1{
    margin-top: auto;
}
.pagina-imobiliare * :not(.nav-bar-3 *, i){
    font-family: Roboto;
}
.bla{
    margin-left: 2rem;
}
.Layout-Imobiliare{
    display: flex;
    margin-top: 10vh;
}
.Container-Scris-Layout{
    width: 50vw;
    padding: 0 5vw;
    background-image: linear-gradient(to right, rgb(76, 131, 252) , rgb(240, 85, 240));
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}
.Container-Scris-Layout p{
    color: rgb(173, 173, 173);
    margin-top: 10px;
    line-height: 1.5rem;
}
.Casuta-Principala-Layout{
    margin-top: 5vh;
    
}
.Casuta-Principala-Layout a{
    display: inline-block;
    text-decoration: none;
    color: black;
    font-weight: 600;
    background-color: white;
    padding: 15px 25px;
    border-radius: 25px;
    margin-top: 2rem;
}

.Casuta-Principala-Layout h2{
    font-size: clamp(1.75rem, 3vw, 3.5rem);
}
.Casuta-Principala-Layout h3{
    font-size: clamp(1.75rem, 1.25vw, 3.5rem);
}
.secundara-mica h2{
    font-size: clamp(2rem, 2vw, 3.5rem);
}
.secundara-mica:first-child{
    padding-right: 10px;
}
.secundara-mica:nth-child(2){
    padding-left: 10px;
}
.Casuta-Secundara-Layout{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 15vh;
    
}
.container-imagine-imobiliare{
    width: 50vw;
    display: flex;
    align-items: center;
}
.container-imagine-imobiliare img{
    width: 100%;
}

.loop-video{
    position:fixed;
    /*background-size: cover;*/
    right: 0;
    bottom: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;/*asta imi lipsea ca sa pot pune width si height 100% fara sa se dea resize automat*/
    filter: brightness(85%)/*de tinut minte*/
    
}



.obstacol-inatltime-100vh{
    width: 100%;
    height: 100vh;
    background-color: white;
    z-index: -2;
    position: relative;
}

.a-doua-parte{
    width: 100%;
    height: 100vh;
    
}

.titlu-parte-2{
    font-size: 3rem;

}


.container-titlu-parte-2{
    position: absolute;/*era spatiu de 10vh intre parte 1 si productcard din cauza position sticky din addeventlistener de la scroll deci am pus 
    position: absolute; ca sa mplu spatiul*/
    width: 80%;
    top: 103.5vh;
}

.container-titlu-parte-2::after{
    content: "";
    width: 100%;
    height: 2px;
    background-color: rgb(51, 153, 119);
    left: 0;
    bottom: -1vh;
    position: absolute;
}

.container-container-titlu-parte-2{
    display: flex;
    justify-content: center;
}

.container-prezentare-parte-2{
    width: 80%;
    margin-top: 2rem;
    margin-bottom: 2rem;
}

.prezentare-parte-2{
    font-size: 1.5rem;
}

.container-container-despre-proiect-1{
    display: flex;
    justify-content: center;
    margin-top: 12rem;
}
.container-container-despre-proiect-2{
    display: flex;
    justify-content: center;
    margin-top: 6rem;
}

.container-titlu-despre-proiect{
    width: 80%;
    position: relative;/*fara sa precizez pozitia elementul .titlu-despre-proiect::after se randa la inceputul "partii 2" 
    ca si cum .container-titlu-despre-proiect nu ar exista(ca si cum ar fi fost absolute si nu ar fi ocupat spatiu in pagina)*/
}


.titlu-despre-proiect {
    font-size: 3rem;
}

.titlu-despre-proiect::after{
    content: "";
    width: 100%;
    height: 2px;
    background-color: rgb(51, 153, 119);
    left: 0;
    bottom: -2vh;
    position: absolute;
}

.container-caseta-display-apartamente{
    margin-top: 10rem;
    padding: 2rem;
}

.buton1-prima-pagina{
    margin-right: 2rem;
    transition: 0.5s;
}
.buton1-prima-pagina a{
    display: inline-block;
    text-decoration: none;
    color: white;
    background-color: rgb(20, 62, 179);
    padding: 18px 24px;
    border-radius: 8px;
    text-transform: uppercase;
    
}
.buton1-prima-pagina:hover{
    transform: scale(1.1);
    transition: 0.5s;
}
.buton2-prima-pagina{
    transition: 0.5s;
}
.buton2-prima-pagina a{
    display: inline-block;
    text-decoration: none;
    color: white;
    background-color: rgb(20, 159, 28);
    padding: 18px 24px;
    border-radius: 8px;
    text-transform: uppercase;
}
.buton2-prima-pagina:hover{
    transform: scale(1.1);
    transition: 0.5s;
}
@media (max-width: 960px){
    .container-imagine-imobiliare{
        display: none;
    }
    .Container-Scris-Layout{
        width: 100vw;
    }
    .Container-Scris-Layout p{
        max-width: 600px;
    }
}
@media (max-width: 500px){
    .container-butoane-prima-pagina{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .buton1-prima-pagina{
        margin-right: 0;
    }
    .buton2-prima-pagina{
        margin: 3rem 0;
    }
}



